body {
  overflow: hidden;
}
.n {
  position: absolute;
  top: 100px;
  margin-left: 50px;
  color: white;
  font-size: 1rem;
}
.n img {
  display: none;
}

nav {
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  letter-spacing: 3px;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

nav a {
  text-decoration: none;
  transition: 0.3s ease;
  margin-right: 20px;
  color: white;
}
nav a:hover {
  text-decoration: underline;
}
#block {
  position: absolute;
  height: 100px;
  width: 100px;

  z-index: 100;
}
#container {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  pointer-events: none;
}
ul {
  position: fixed;
  top: 1s0;

  right: 0;
  z-index: 100;
  border: 1px solid #fff;
  width: 50px;

  list-style: none;
  margin: 0;
  padding: 0;
}
li {
  font-size: 1rem;
  color: white;
  cursor: pointer;
  border: 1px solid white;
}
